import React from 'react'
import LandingPage from './LandingPage'
import Work from './Work'
import $ from "jquery"
import { gsap } from "gsap";
import '../css/style.css'
$(document).ready(function(){
    $(".closeMenuToWork").click(function(){
        console.log("closedToWork");
        $(".article1").css("display", "none");
        $(".work").css("display", "block");
        gsap.to(".text", {duration: 0.3, opacity: 0 })
        gsap.to(".text", {delay:0.3, duration: 0, display: "none" })
        gsap.to(".third__layer", {delay: 0.7, duration: 0.7, width: 0})
        gsap.to(".secound__layer", {delay: 0.5 ,duration: 0.2, width: 0})
        gsap.to(".first__layer", {delay: 0.3, duration: 0.2, width: 0})
    })
})

function Home() {
    return (
        <div>
            <LandingPage/>
            <Work/>
            
        </div>
    )
}

export default Home
